import React from 'react';
import styled from 'styled-components';
import { Card } from "primereact/card";
import { Avatar } from "primereact/avatar";

const CommentItem = ({avatar, text, name}) => (
    <CommentContainer>
        <Card>
            {text.map(el => <p key={el.substr(0,10)}>{el}</p>)}
            <div className="person_container">
                {avatar ? <Avatar image={avatar} shape="circle" /> : <Avatar label={name[0]} shape="circle" />}
                <b>{name}</b>
            </div>
        </Card>
    </CommentContainer>
);

export default CommentItem;

const CommentContainer = styled.div`
  padding: 20px;
  
  .person_container {
    display: grid;
    grid-template-columns: 32px 1fr;
    grid-gap: 10px;
    align-items: center;
  }
  .p-avatar {
    border-radius: 50%;
  }
`